<template>
    <header class="header fixed-top t-center">
        <div class="effect">
            <header-bar></header-bar>
        </div>
    </header>
    <footer class="footer fixed-bottom t-center">
        <div class="effect">
            <tab-bar></tab-bar>
        </div>
    </footer>
    <div class="wrap-page">
        <router-view></router-view>
    </div>
</template>

<script>
import HeaderBar from './components/HeaderBar'
import TabBar from './components/TabBar'

require('assets/css/reset.css')
require('assets/css/common.css')
// require('assets/css/lib/font-awesome-4.7.0/css/font-awesome.min.css')
require('font-awesome-webpack')

export default {
  components: {
    HeaderBar,
    TabBar
  }
}
</script>

<style scoped>
html, body, .wrap-page {
    height: 100%;
}
.header,
.footer,
.wrap-page {
    position: absolute;
    left: 0;
    right: 0;
}
.header,
.footer {
    background-color: #fff;
    z-index: 900;
}
.header {
    height: 45px;
    line-height: 45px;
    top: 0;
    border-bottom: 1px solid #b7b7b7;
    color: #fff;
    background: linear-gradient(180deg,#303036,#3c3b40);
}
.footer {
    height: 50px;
    bottom: 0;
    border-top: 1px solid #b7b7b7;
}
.wrap-page {
    -webkit-overflow-scrolling: touch;
    background-color: #efeff4
}
.header ~ .wrap-page {
    padding-top: 45px;
}
.header-sub ~ .wrap-page {
    padding-top: 88px;
}
.footer ~ .wrap-page {
    padding-bottom: 50px;
}
.fixed-top {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 960;
}
.fixed-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 940;
}
.effect {
    opacity: 1;
    transition: all .3s ease;
}
</style>



