<template>
    <ul class="nav-links">
        <li
         v-for="item in tab"
         class="nav-links_item "
         data-role="nav-links_item"
         v-link="item.link"
         @click="selectActive($index)">
            <span class="nav-links_icon">
                <i class="fa" :class="item.icon" aria-hidden="true"></i>
            </span>
            <p class="nav-links_label" v-text="item.label"></p>
        </li>
    </ul>
</template>
<script>
export default {
    created () {
        // 设置当前tab
        // console.log(this.$route);
        //var current = this.$route.path;
        //for(var i=0,len=this.tab.length; i<len; i++) {
            //var t = this.tab[i]
            //if (t.link === current) {
                //t.isOn = true
            //} else {
                //t.isOn = false
            //}
        //}
    },
    data () {
        return {
            tab: [
                {
                    label: "首页",
                    icon: "fa-comment",
                    link: "/home",
                    isOn: true
                },
                {
                    label: "通讯录",
                    icon: "fa-address-book-o",
                    link: "/contact",
                    isOn: false
                },
                {
                    label: "发现",
                    icon: "fa-compass",
                    link: "/discover",
                    isOn: false
                },
                {
                    label: "我",
                    icon: "fa-user-o",
                    link: "/me",
                    isOn: false
                }
            ]
        };
    },
    methods: {
        selectActive (index) {
            //for(var i=0,len=this.tab.length; i<len; i++) {
                //var t = this.tab[i]
                //t.isOn = false
            //}
            //this.tab[index].isOn = true
        }
    }
}
</script>
<style scoped>
.nav-links {
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
}
.nav-links_item {
    display: block;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    font-size: 0;
    color: #999;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    padding: 3px 0 0;
}
.nav-links_item:hover {
    cursor: pointer;
}
.nav-links_item.v-link-active {
    color: #09BB07
}
.nav-links_item_on {
    color: #09BB07;
}
.nav-links_icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    font-size: 24px;
    line-height: 24px;
}
.nav-links_label {
    font-size: 12px;
    margin-top: 3px;
    margin-bottom: 2px;
}
</style>



