<template>
    <div class="alpha-cells">
        <template v-for="cell in cells">
            <a class="alpha-cell alpha-cell_link" href="javascript:;" v-link="cell.link" v-if="cell.withLink">
                <div class="alpha-cell_hd" v-if="cell.icon">
                    <img :src="cell.icon" alt="cell.title">
                </div>
                <div class="alpha-cell_bd">
                    <p class="alpha-cell_title" v-text="cell.title"></p>
                    <p class="alpha-cell_subtitle" v-text="cell.subTitle" v-if="cell.subTitle"></p>
                </div>
                <div class="alpha-cell_ft">
                    <p v-if="cell.others" v-text="cell.others"></p>
                </div>
            </a>
            <div class="alpha-cell" href="javascript:;" v-else>
                <div class="alpha-cell_hd" v-if="cell.icon">
                    <img :src="cell.icon" alt="cell.title">
                </div>
                <div class="alpha-cell_bd">
                    <p class="alpha-cell_title" v-text="cell.title"></p>
                    <p class="alpha-cell_subtitle" v-text="cell.subTitle" v-if="cell.subTitle"></p>
                </div>
                <div class="alpha-cell_ft">
                    <p v-if="cell.others" v-text="cell.others"></p>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
export default {
    data () {
        return {
            withLink: true,
            imgUrl: require('../assets/images/list-icon.png'),
            cells: [
                {
                    icon: require('../assets/images/list-icon.png'),
                    title: "列表标题1-1" ,
                    others: "说明文字1",
                    link: "/contact",
                    withLink: true
                },
                {
                    title: "列表标题2-1" ,
                    subTitle: "列表副标题2-2",
                    others: "说明文字2",
                    link: "/me",
                    withLink: false
                },
                {
                    icon: require('../assets/images/list-icon.png'),
                    title: "列表标题3-1" ,
                    subTitle: "列表副标题3-2",
                    link: "/me",
                    withLink: false
                },
                {
                    title: "列表标题4-1" ,
                    subTitle: "列表副标题4-2",
                    link: "/me",
                    withLink: true
                }
            ]
        }
    }
}
</script>

<style lang="stylus">
@import '../assets/css/src/alpha-cells.styl'

</style>



