<template>
    <div class="m-center f19">
        Just Chat
        <span class="new-msg-count" data-role="new-msg-count" v-show="msgTip.hasNew">({{msgTip.count}})</span>
    </div>
    <div class="p-relative others">
        <action-bar></action-bar>
    </div>
</template>

<script>
import ActionBar from './ActionBar'
export default {
    data () {
        return {
            msgTip: {
                hasNew: true,
                count: 100
            }
        }
    },
    components: {
        ActionBar
    }
}
</script>

<style lang="stylus" scoped>
.others
    top: -45px
    left: 0
    right: 0
</style>



